import { Component, OnInit } from "@angular/core";
import { DashboardService } from "../../services/dashboard.service";
import { StoreService } from "../../../shared/services/store.service";

@Component({
    selector: "app-main",
    templateUrl: "./main.component.html",
    styleUrls: ["./main.component.scss"]
})
export class MainComponent implements OnInit {
    constructor(
        public storeService: StoreService,
        public dashBService: DashboardService
    ) {}

    ngOnInit() {
        // Active Utilisateurs que si l'un des modules a activer membership
        if (this.storeService.curSite) {
            if (
                this.storeService.curSite.uids.find(
                    uid => uid.nom === "membership"
                )
            ) {
                this.dashBService.itemsMenuSidebar.find(
                    item => item.name === "Utilisateurs"
                ).visible = true;
            }
        }
    }

    showDomainNames() {
        alert("coming soon @78");
    }
    showProfile() {
        alert("coming soon @79");
    }
    showHelp() {
        alert("coming soon @80");
    }
}



