import { Injectable } from "@angular/core";
import { Subject } from "rxjs/Subject";
import { MenuItem } from "../../shared/classes/menu";
import { ModalService } from "../../shared/services/modal.service";
import { Router } from "@angular/router";

@Injectable()
export class DashboardService {
    // Observable des events qui se passe sur le dashboard
    events = new Subject<any>();

    cssMenuItem = "we-sidebar-nav-item we-sidebar-nav-link we-w-100";
    cssIconR = "";
    iconR = {
        active: true,
        className:
            " we-btn-icon we-align-self-center text-greyX fal fa-angle-right"
    };

    itemsMenuSidebar: MenuItem[] = [
        {
            iconL: { active: true, className: "fal fa-newspaper" },
            iconR: this.iconR,
            className: this.cssMenuItem,
            name: "Blog",
            linkFill: true,
            command: () => this.router.navigateByUrl("/blog/articles"),
            type: "default"
            // routerLink: "/blog/articles",
            // type: "page"
        },
        {
            iconL: { active: true, className: "fal fa-shopping-cart" },
            iconR: this.iconR,
            className: this.cssMenuItem,
            name: "E-Commerce",
            linkFill: true,
            command: () => this.router.navigateByUrl("/shop"),
            type: "default"
            // routerLink: "/shop",
            // type: "page"
        },



        {
            iconL: { active: true, className: "fab fa-wpforms" },
            iconR: this.iconR,
            className: this.cssMenuItem,
            name: "Formulaires",
            linkFill: true,
            command: () => this.router.navigateByUrl("/formulaire"),
            type: "default"
            // routerLink: "/shop",
            // type: "page"
        },
        {
            iconL: { active: true, className: "fas fa-users" },
            iconR: this.iconR,
            className: this.cssMenuItem,
            name: "Utilisateurs",
            linkFill: true,
            command: () => this.router.navigateByUrl("/utilisateur"),
            type: "default",
            visible: false
            // routerLink: "/shop",
            // type: "page"
        },
        {
            iconL: { active: true, className: "fal fa-comment" },
            iconR: this.iconR,
            className: this.cssMenuItem,
            name: "Commentaires",
            linkFill: true,
            command: () => this.router.navigateByUrl("/commentaire"),
            type: "default"
            // routerLink: "/shop",
            // type: "page"
        },
     
        // {
        //     iconL: { active: true, className: "fal fa-globe" },
        //     // iconR: this.iconR,
        //     className: this.cssMenuItem,
        //     name: "Nom de domaine",
        //     linkFill: true,
        //     command: () => this.openParams(),
        //     type: "default"
        // }
    ];

    constructor(private modalService: ModalService, private router: Router) {}

    openParams() {
        this.modalService.open("modalParamsGeneral");
    }
}



