import { Component, OnInit, OnChanges, SimpleChanges } from "@angular/core";
import { MenuItem } from "../../../shared/classes/menu";
import { Router } from "@angular/router";
import { DashboardService } from "../../services/dashboard.service";
import { StoreService } from "../../../shared/services/store.service";
import { GetMainDomainPipe } from "../../../shared/pipes/get-main-domain.pipe";
import { environment } from "../../../../environments/environment";
import { Domain, Site } from "../../../api";
import { PermissionService } from "../../../shared/services/permission.service";
import { ModalService } from "../../../shared/services/modal.service";

@Component({
    selector: "app-accueil",
    templateUrl: "./accueil.component.html",
    styleUrls: ["./accueil.component.scss"]
})
export class AccueilComponent implements OnInit, OnChanges {
    srcImgSite = "";
    mainDomain: Domain;
    isSubDomain: boolean = true;
    constructor(
        public dashBService: DashboardService,
        public store: StoreService,
        public getMainDomainPipe: GetMainDomainPipe,
        public permService: PermissionService,
        private modalService: ModalService
    ) {}

    ngOnChanges(changes: SimpleChanges): void {
        console.log("changes", changes);
    }

    ngOnInit() {
        if (!this.store.curSite) {
            return;
        }
        this.initImageSite();
        this.mainDomain = this.getMainDomainPipe.transform(
            this.store.curSite.domains
        );
        this.isSubDomain =
            this.mainDomain.name.indexOf(`.${environment.domain}`) !== -1;
    }

    onClickIcon(item: MenuItem) {
        if (typeof item.command === "function") {
            item.command();
        }
    }

    initImageSite() {
        if (!this.store.curSite.thumb) {
            this.srcImgSite = "assets/img/wifeo_banner.png";
        } else {
            this.srcImgSite = this.store.curSite.thumb;
        }
    }

    imgError(image) {
        image.onerror = "";
        image.src = "assets/img/wifeo_banner.png";
        return true;
    }

    onSaveSite(site: Site) {
        this.store.curSite = site;
        const strMsg = JSON.stringify({
            siteChange: site
        });
        parent.postMessage(strMsg, "*");
    }

    clickOnConnectDomain() {
        const strMsg = JSON.stringify({
            navigateByUrl: "dashboard/domaines"
        });
        parent.postMessage(strMsg, "*");
    }

    clickOnEditSubDomain() {
        this.modalService.open("modalParamsGeneral");
    }
}



